<template>
  <section class="charts">
    <h3>Dynamic Chart</h3>
    <vue-highcharts :options="options" />
  </section>
</template>

<script>
  import io from 'socket.io-client'
  import axios from 'axios'
  import VueHighcharts from "vue2-highcharts";
  import Highcharts from "highcharts";


  const API_URL = 'http://0.0.0.0:5000/tf_api';
  function getHistorical(e) {
    axios.post(// `${API_URL/test/random`, {
       `${API_URL}/historical/?offset=650000&max_points=100`, {
      // body: {
      //   offset: 650000,
      //   max_points: 100
      // }

    }).then(function(response) {
      console.log(response.data);
      this.data = response.data
    })
      // .catch(error => {
      //   console.log(error);
      // });
  }

  // let hist = getHistorical();
  // console.log(hist);

  let socket =  io.connect('http://0.0.0.0:5000');

  socket.on('connect', function() {

    console.log('Websocket connected!');
  });

  const data = {
    chart: {
      type: "spline",
      zoomType: 'x',
      animation: Highcharts.svg, // don't animate in old IE
      marginRight: 10,
      events: {
        load: function () {
          let socket =  io.connect('http://0.0.0.0:5000');
          let self = this;
          // let series = this.series[0];
          setInterval(function () {
            socket.on('price_event', function (event) {

              let datetime = new Date(event.ts);
              let x = new Date(datetime),
                y = event.price;
              console.log(x.getTime());
              console.log(y);
              self.series[0].addPoint([x.getTime() - x.getTimezoneOffset()*60000, y]);//, true, true);
            });


          })
        }


      },
    },
    title: {
      text: "Live random data"
    },
    scrollbar: {
      liveRedraw: true
    },

    xAxis: {
      type: "datetime",
      tickPixelInterval: 10,
      // afterSetExtremes: getHistorical,
      minRange: 1000 // one se
    },
    yAxis: {
      floor: 0,
      title: {
        text: "Value"
      },

      plotLines: [
        {
          value: 0,
          width: 1,
          color: "#808080"
        }
      ]
    },
    tooltip: {
      formatter: function() {
        return (
          "<b>" +
          this.series.name +
          "</b><br/>" +
          Highcharts.dateFormat("%Y-%m-%d %H:%M:%S", this.x) +
          "<br/>" +
          Highcharts.numberFormat(this.y, 2)
        );
      }
    },
    legend: {
      enabled: false
    },
    exporting: {
      enabled: true
    },
    series: [
      {
        name: "Random data",
        data: (function () {
          return getHistorical();
            //return resp.data
          }
        )()
        // data: (function() {
        //   // generate an array of random data
        //   let data = [],
        //     time = Date.now(),
        //     i;
        //
        //   for (i = -19; i <= 0; i += 1) {
        //     data.push({
        //       x: time + i * 1000,
        //       y: Math.random()*5 + 6850
        //     });
        //   }
        //   return data;
        // })()
      }
    ]
  };
  export default {
    components: {
      VueHighcharts
    },
    data() {
      return {
        options: data
      };
    }
  };


</script>



